.menu {
  background: $white;
  color: $off-black;
  padding: $m-size;
  vertical-align: middle;
  height: $xxl-size;
  position: absolute;
  justify-content: flex-start;
  margin: auto;
  left: $xxl-size;
  display: flex;
  width: 100%;
  top: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
  z-index: 999;
}



