@import url('https://fonts.googleapis.com/css?family=Roboto');

html {
  font-size: 62.5%;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: $m-size;
}

.leaflet-container {
  display: block;
  position: absolute;
  top: 0;
  left: $xxl-size;
  right: $xxl-size-plus;
  border: none;
  height: 100%;
}

.smallmap {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  border: none;
  height: 40%;
}



